            tbody
              tr
                td(style="vertical-align: initial") Primera infancia
                td(style="background-color: #F0F3FA") La primera infancia cobija desde la gestacin hasta los 5 aos de vida. Las nias y los nios de primera infancia son seres sociales, diversos y singulares, que interactan con el mundo y despliegan todas sus potencialidades, movidos por su deseo de expresarse, conocer, explorar, experimentar, descubrir, moverse y comprender quines son, de qu son capaces, cmo funcionan las cosas que les rodean, cmo lo pueden transformar, cmo se convive con otros seres humanos, cmo se comunican. En medio de estas interacciones tiene lugar su desarrollo. Desde esta comprensin es fundamental el apoyo y promocin de este proceso de desarrollo a nivel fsico, social, afectivo, del lenguaje y del pensamiento; a partir de un cuidado carioso y sensible a las necesidades del nio o nia, conociendo y acompaando el proceso de desarrollo y protegindolo ante los riesgos.
                td(rowspan="2", style="vertical-align: initial") Acompaar a padres y cuidadores en el fortalecimiento de sus capacidades para la crianza, el cuidado de la salud y proteccin que promuevan la salud y el desarrollo humano integral de nios, mediante un proceso de construccin colectiva de saberes y prcticas para el cuidado y proteccin propios, de sus familias y de los entornos.
              tr
                td(style="vertical-align: initial") Infancia
                td(style="background-color: #F0F3FA") Comprende entre los 6 y los 11 aos y supone la experiencia de consolidacin del proceso de desarrollo que viene configurndose desde la primera infancia y materializa el trnsito a nuevos aprendizajes que resultan de mayores complejidades en el uso o desarrollo de capacidades, habilidades y potencialidades, lo cual redunda en logros progresivos de autonoma e independencia, unido a mayores interacciones y riqueza relacional. Los nios y nias entre los 6 y los 11 aos de edad tienen muchas potencialidades para aprender, cada vez se hacen ms conscientes de sus capacidades, por lo que van aumentando su interaccin social fuera de la familia. Suelen disfrutar de juegos de reglas y competencia, logran resolver problemas cotidianos de manera independiente y aprenden imitando lo que ven en las personas de su entorno. Este es un momento muy importante para incentivarlos a adoptar pasatiempos que les ayuden a estar ms saludables y felices.
              tr
                td(style="vertical-align: initial") Adolescencia
                td(style="background-color: #F0F3FA") La adolescencia se considera el momento vital entre los 12 y los 17 aos, es considerada un momento vital caracterizado por profundas transformaciones, cuyo dinamismo trae consigo crisis, conflictos y contradicciones, necesarias para definir la identidad.
                  |
                  br
                  br
                  | Como parte del proceso de construccin identitaria cada adolescente se replantea su definicin personal y social, a travs de procesos de diferenciacin del grupo familiar y de bsqueda de pertenencia y de sentido de vida. En este proceso de construccin y logro de autonoma tienen un papel fundamental la capacidad de reflexin y de toma de decisiones, as como los entornos, la relacin con sus pares y su contexto.
                  |
                  br
                  br
                  | Los adolescentes estn en un momento de su vida en el que se presentan profundas transformaciones biolgicas, psicolgicas y sociales, muchas de ellas pueden generar crisis, conflictos y contradicciones; pero esencialmente son  cambios positivos, pues no es solamente un periodo de adaptacin a cambios corporales, sino una fase de grandes determinaciones hacia una mayor independencia psicolgica y social, siendo un momento clave en el proceso de socializacin con importantes aprendizajes que permiten fortalecer las interrelaciones personales para distinguirse de otros y adaptarse a la sociedad
                td(style="vertical-align: initial") a. Reforzar las potencialidades de todos los adolescentes, en lo relacionado con su capacidad de agencia, autonoma y construccin de identidad.
                  |
                  br
                  br
                  | b. Acompaar al adolescente en el reconocimiento, comprensin y reflexin sobre los procesos emergentes del momento vital relacionados con los cambios fisiolgicos, psicolgicos, proyecto de vida, la sana convivencia, la sexualidad y la reproduccin.
                  |
                  br
                  br
                  | c. Desarrollar o potenciar capacidades para el ejercicio de sus derechos, con el fin que estos procesos contribuyan a su desarrollo integral y su calidad de vida presente y futura, as como para el cuidado de su salud.
                  |
                  br
                  br
                  | d. Desarrollar capacidades en los padres y cuidadores que les permitan comprender y gestionar los cambios que suceden en la adolescencia, encontrar formas de renegociar las relaciones parento- filiales, resolver los problemas de forma productiva, apoyar el desarrollo de la autonoma e identidad y mantener un vnculo positivo y de apoyo emocional.
              tr
                td(style="vertical-align: initial") Juventud
                td(style="background-color: #F0F3FA") Comprende la trayectoria entre los 18 y los 28 aos, se comprende como un momento de afirmacin en el que el escenario social se convierte en el nicho para concretar y estructurar los intereses y propsitos que se han venido vislumbrando y potenciando desde la niez y que ahora entran en interaccin con las condiciones y oportunidades del contexto, adentrndose en la experiencia de asumir la construccin de una vida independiente que se consolida en la edad adulta. Los y las jvenes se caracterizan por su entusiasmo y energa permanente, es el momento de consolidacin de la autonoma intelectual, fsica, moral, econmica, social y cultural, lo que se evidencia en actitudes de seguridad, poder y dominio. La juventud impacta a una generacin completa y se expresa con vigor en todos los campos: familiar, educativo, laboral, etc.
                td(style="vertical-align: initial") Desarrollar capacidades en las y los jvenes para transformar saberes y prcticas en pro de una vida saludable, relacionadas con el ejercicio de la sexualidad y reproduccin libre e informada, la sana convivencia, la educacin para el trabajo y el desarrollo humano en los diferentes escenarios durante el curso de vida, la capacidad poltica y la autonoma para agenciarse y relacionarse con otros/as.
              tr
                td(style="vertical-align: initial") Adultez
                td(style="background-color: #F0F3FA") Comprende el momento de vida que transcurre entre los 29 a 59 aos.  Se configura en un proceso dinmico, en donde contina el desarrollo y las personas son capaces de establecer metas y poner en marcha los recursos necesarios para alcanzarlas, configurando sus trayectorias evolutivas personales y aprovechando las oportunidades disponibles en sus contextos. 
                  |
                  br
                  br
                  | En este sentido, es importante subrayar que los patrones de desarrollo adulto no se configuran en algo novedoso, sino en la consolidacin de los aprendizajes previos y un perfilamiento cada vez ms claro de la individualidad.
                td(style="vertical-align: initial") Desarrollar capacidades con los adultos para la promocin y mantenimiento de su salud, con la finalidad de aportar al goce efectivo del derecho a la salud, mediante un proceso de construccin colectiva de saberes y prcticas.
              tr
                td(style="vertical-align: initial") Vejez
                td(style="background-color: #F0F3FA") Se considera persona mayor a toda persona desde los 60 aos. En este momento de la vida se disfruta a partir de los aprendizajes y experiencia acumulada. Existen diversos enfoques para comprender el proceso de envejecimiento y las caractersticas de la vejez; sin embargo, estos pueden vivirse desde las prdidas o desde la plenitud, de acuerdo con los efectos acumulados a lo largo de la vida, la confluencia de las oportunidades y los recursos, tanto individuales como generacionales, que afectan el continuo de la vida de cada persona, as como su condicin y posicin social y su conjugacin con la edad, el gnero, la clase social y el origen tnico, que representan ciertas diferencias en el acceso y disfrute de dichos recursos y oportunidades.
                td a. Desarrollar capacidades con las personas adultas mayores para la promocin y mantenimiento de su salud, con la finalidad de aportar al goce efectivo del derecho a la salud, mediante un proceso de construccin colectiva de saberes y prcticas.
                  |
                  br
                  br
                  | b. Mantener conservadas el mayor tiempo posible las habilidades intelectuales de atencin, memoria, funciones ejecutivas, aprendizaje, razonamiento, percepcin, clculo, praxias, as como la autonoma en las personas adultas mayores, a travs del entrenamiento cognitivo y emocional, el fortalecimiento de habilidades de afrontamiento del estrs, el fortalecimiento de lazos familiares y la promocin de la participacin en redes sociales.
