      .col-lg-8(data-aos="fade-left")
        p El curso de preparacin para la maternidad y la paternidad es un proceso organizado de educacin, realizado de manera interactiva entre el equipo de profesionales de la salud y la mujer con su compaero, sus hijos(as) y su familia o con la persona que ella considere afectivamente ms cercana. La intencionalidad es desarrollar capacidades para el cuidado de la salud durante la gestacin, el parto y el puerperio. Se plantea que para este curso se pueda contar con un equipo interdisciplinario liderado por enfermera (dada la experiencia y conocimiento en relacin con los procesos de cuidado) e idealmente compuesto por psicologa, nutricin, terapia fsica, medicina general y especializada (ginecologa y pediatra) con el apoyo de otros profesionales de la salud que se definan de acuerdo con la necesidad.
