        p.mb-0 #[b Consideraciones en relacin con las mujeres gestantes y sus familias]
    p.mb-4(data-aos="fade-right") Desde el enfoque de curso de vida, la maternidad y la paternidad se conciben como un suceso vital, que implica cambios fsicos, psicolgicos y emocionales en la mujer gestante que promueven o ponen en riesgo la salud fsica y mental de la mujer, y la de su hijo/a por nacer. Las condiciones personales, as como las redes de apoyo con las que cuentan las mujeres cuando inician o planean su gestacin son determinantes en los resultados de su salud y de sus recin nacidos, dejan una impronta en los siguientes momentos del curso de la vida.
    .row.align-items-center.mb-5
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema3/img20.png', style="width: 350px" ,alt='Texto que describa la imagen').m-auto
      .col-lg-8(data-aos="fade-left")
        p Acompaar el proceso, fortalecer capacidades para el cuidado, garantizar atenciones en salud (incluso desde la preconcepcin), son algunas de las intervenciones ms costo-efectivas en la primera infancia y favorece el establecimiento de un vnculo seguro. Este proceso debe mantener el centro tanto en la madre y el padre como sujetos de cuidado de nios y nias; sin perder la perspectiva de abordaje de las y los mismos como sujetos de atencin que permita favorecer el apoyo y el acompaamiento en este proceso clave en la trayectoria vital de las mujeres y los hombres, considerando condiciones socioeconmicas y directamente redes de apoyo que favorecen una vivencia digna del proceso y considerando enfoques de gnero, diferencial y de curso de vida en un marco de derechos.
