                .col-auto.px-0
                  figure
                    img(src='@/assets/curso/tema1/img7.svg', style="width: 60px" ,alt='Texto que describa la imagen').m-auto
                .col
                  p.mb-0 Los objetivos se plantean en trminos de desarrollar capacidades en mujeres gestantes, parejas y familias, para comprender los cambios asociados a la gestacin, para promover el cuidado de la salud, detectar y gestionar los riesgos; y las madres, los padres y sus familias para vivir la gestacin, el parto, el puerperio y el cuidado del recin nacido de manera sana y psicolgicamente armoniosa. Los elementos a considerar para el abordaje de mujeres gestantes y sus familias se presentan en el siguiente video:
