        p Partir del reconocimiento de las condiciones y situaciones de las mujeres gestantes, pareja o familia que acompaan en la primera sesin. Valorar cules pueden ser esas condiciones en relacin al cuidado a las que quisieran llegar, cmo quisieran estar y sentirse durante esta gestacin, qu ilusiones y tambin qu      inquietudes estn presentes en este proceso como mujeres y como familias; para tener una base que interpele con los resultados en salud planteados para el curso, los cuales pueden comunicarse de manera sencilla para que se entienda ms fcilmente lo importante de las prcticas de cuidado que no dependen nicamente de la mujer, el fortalecer ese apoyo que se brinda desde la familia y la comunidad, la necesidad de identificar signos de alarma para a su vez activar mecanismos comunitarios e institucionales de respuesta y finalmente, la importancia de los vnculos y el cuidado en la primera infancia, especialmente en ese periodo del primer mes. Desde all, es posible plantear esas capacidades que se quieren potenciar durante el curso como proceso.
