    .titulo-segundo.color-acento-contenido
      h2 3.3 Planteamiento de educacin para la salud en entornos
    .row.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0(data-aos="fade-right")
        p.mb-0 La educacin para la salud colectiva tiene como objetivo favorecer los procesos de desarrollo tanto de las personas como de las familias y comunidades, empoderar a los sujetos, consolidar procesos de cuidado (de s, con otros y con el entorno), fortalecer la capacidad de afrontamiento, agencia y fortalecer los vnculos, redes y relaciones para el cuidado y el desarrollo. Est definido como un proceso que debe desarrollarse como mnimo dos ciclos al ao por grupo poblacional o colectivo priorizado en el entorno, donde cada ciclo est compuesto por 9 a 12 sesiones con el mismo grupo (cada sesin de 2 a 3 horas). En el caso de una zona rural dispersa, se realizar mnimo un ciclo al ao por grupo poblacional o colectivo priorizado; si el rea rural es de difcil acceso o queda a ms de cuatro horas de la cabecera municipal, se podr desarrollar un ciclo al ao.
