      .col-10.col-lg-auto.bg-c18
        p.mb-0 #[b Consideraciones con relacin a los sujetos en los entornos y objetivos de la EpS colectiva]
    p.mb-5(data-aos="fade-right") La Poltica de Atencin Integral en Salud como desarrollo de la Ley Estatutaria coloca a los sujetos en el centro, reconoce a  los sujetos de derecho, sujetos en contexto, sujetos activos y vinculados a una red de relaciones donde tiene lugar su trayectoria. El entorno es un lugar de encuentro donde se construyen y dinamizan relaciones y vnculos que por dems afianzan y favorecen o inciden en el desarrollo, la salud y el cuidado. Por lo tanto, la apuesta al abordar un entorno es mantener la mirada en los sujetos que en l se desarrollan.
    .row.mb-5(data-aos="fade-down")
      .col-12.bg-c3
        .p-4
          .row.align-items-center
            .col-md-auto.mb-4.mb-md-0
              figure
                img(src='@/assets/curso/tema3/img43.svg', style="width: 100px" ,alt='Texto que describa la imagen').m-auto
            .col
              p #[b Los entornos se definen como los escenarios de la vida cotidiana en los cuales los sujetos se desarrollan, donde constituyen su vida subjetiva, construyen vnculos y relaciones con la vida social, histrica, cultural y poltica de la sociedad a la que pertenecen. Son escenarios configurados por dinmicas y por condiciones sociales, fsicas, ambientales, culturales, polticas y econmicas, donde las personas, las familias y las comunidades conviven y en los que se produce una intensa y continua interaccin y transformacin entre ellos y el contexto que les rodea]
              .row.justify-content-end.align-items-center
                .col-auto.pe-0
                  figure
                    img(src='@/assets/curso/tema3/img44.svg', style="width: 20px" ,alt='Texto que describa la imagen')
                .col-auto.pe-4
                  p.mb-0 #[b (Kaplan 2002, p. 146)]
    .row.align-items-center.mb-5
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema3/img27.png', style="width: 450px" ,alt='Texto que describa la imagen').m-auto
      .col-lg-8(data-aos="fade-left")
        p.mb-0 El entorno comunitario se comprende como los espacios donde se da la dinmica social de las personas y los colectivos dispuestos en grupos de base, redes sociales y comunitarias, as como tambin, los espacios de relacin, encuentro y desplazamiento, como son las infraestructuras, bienes y servicios dispuestos en el espacio pblico (Decreto 1504 de 1998). Implica la dinmica que se establece en espacios abiertos, caracterizados por el libre acceso (plazas, plazoletas, parques, zonas de veredas, vas, entre otros) y de espacio, ubicados en infraestructura de acceso permitido a los cuales la comunidad atribuye un valor paisajstico, poltico, histrico, cultural y/o religioso (bibliotecas, ludotecas, casa de la cultura, museos, teatros, templos, malocas, estadios, escenarios de participacin, entre otros) (MinSalud, 2018). En este marco, lo relacional es el factor de conformacin del sujeto colectivo, lo que les da sentido a los procesos de educacin para la salud, ms all de la definicin de un espacio fsico.
