    p.mb-5(data-aos="fade-right") Los resultados, las capacidades a desarrollar, los contenidos de la educacin y la comunicacin para la salud en el marco del Plan de Intervenciones Colectivas (PIC) propone que las sesiones se realicen con conjuntos de personas que habitan un entorno, bien sea en:
