      .col-lg-8(data-aos="fade-left")
        p En el marco del proyecto #[b Orientaciones pedaggicas, metodolgicas, tcnicas y operativas para el desarrollo de capacidades en redes de apoyo comunitarias, para el cuidado de la salud materna e infantil en redes comunitarias. Tejiendo espacios para el cuidado intercultural de la salud en los primeros mil das de vida], desarrollado por el Fondo Colombia en Paz (2021), se realiza una propuesta de EpS en el marco de un proceso de fortalecimiento de redes sociales y comunitarias al que se le propone como centro el cuidado de la salud materno infantil. A partir de esta experiencia que articula las apuestas de construccin de redes con los aportes de los procesos de educacin para la salud, se ejemplifica este proceso en el entorno comunitario.  Para esto se proponen resultados, ncleos temticos y algunos desarrollos, articulados a un ciclo de educacin para la salud, en el marco del cuidado de la salud materna.
        .row
