                  figure
                    img(src='@/assets/curso/tema1/img7.svg', style="width: 60px" ,alt='Texto que describa la imagen').m-auto
                .col
                  p.mb-0 El tema general del ciclo educativo es el  cuidado intercultural de la salud en los primeros mil das de vida. El primer paso para la planificacin del proceso de educacin para la salud es establecer cules son los resultados que se quieren alcanzar o a los cuales se quiere contribuir con el proceso de educacin para la salud; estos resultados estn definidos bien sea en la Resolucin 3280 de promocin y mantenimiento de la salud o bien en las rutas de riesgos de los eventos priorizados. En el siguiente video se presenta el ejemplo del Fondo Colombia en Paz:
