                      p Mensaje clave para la apertura del tema en este ncleo temtico:  el proceso de la reproduccin humana es un tema de inters del individuo, las familias y las comunidades, parte del desarrollo pleno de los derechos sexuales y reproductivos, y del proyecto de vida, as mismo, est muy relacionado con el nivel socioeconmico y cultural de los territorios. 
