                      p Por ejemplo, la sexualidad va ms all de tener relaciones sexuales, parte del reconocimiento y autoidentificacin de las emociones, caractersticas, conducta y prcticas de los individuos, las cuales se vivencian en diferentes momentos del curso de la vida, determinados por las particularidades del territorio donde se desarrollan las personas, las tradiciones y costumbres de los pueblos y a las experiencias propias. 
